// ==============
//    variables
//===============

$color_grey:              #f2f2f2;
$color_grey_darker:       #c5c5c5;
$color_black:             #282C34;
$color_primary:           #F23168;
$color_secondary_dark:    #043464;
$color_secondary_light:   #51CEFA;
$color_green:             #4CBB17;
$color_red:               #FE0002;

// ==============
//    mixins
//===============
@mixin desktop-sm{
    @media only screen and (max-width: 1200px) {
        @content;
    }
}
@mixin tablet{
    @media only screen and (max-width: 1024px) {
        @content;
    }
}
@mixin mobile{
    @media only screen and (max-width: 768px) {
        @content;
    }
}
@mixin mobile-sm{
    @media only screen and (max-width: 480px) {
        @content;
    }
}
@mixin portrait{
    @media only screen and (max-width: 768px) and (orientation: portrait){
        @content;
    }
}
@mixin landscape{
    @media only screen and (max-width: 768px) and (orientation: landscape){
        @content;
    }
}
