// =====================
//  global app styling
//======================
.sr-only {
    border: 0; 
    clip: rect(1px, 1px, 1px, 1px); 
    clip: rect(1px 1px 1px 1px); //IE6 and IE7 
    clip-path: inset(50%); 
    height: 1px; 
    margin: -1px; 
    overflow: hidden; 
    padding: 0; 
    position: absolute; 
    width: 1px; 
    // word-wrap: normal !important;

}
.sr-only--is-focusable:focus{
    position: static;
    clip: auto !important; 
    clip-path: none; 
    height: auto;
    margin: 0;
    overflow: auto;
    line-height: normal; 
    width: auto; 
    padding:3rem 5rem;
    left: 0;
    right: 0;
    background: orange;
    color: #000;
    font-size: 1.4rem;
    font-weight: bold;
    z-index: 1000;
    display: block;
 
}
.sr-only--is-focusable:hover{
    text-decoration-line: underline;
}
p{
    margin-top: 1rem;
}
.p_larger{
    font-size: 3.5rem;
    line-height: 1.4;
    margin-top: 4rem;
}
.p_large{
    font-size: 2.5rem;
    line-height: 1.4;
    margin-top: 3rem;
}
.fill{
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}
.fill_centered{
    display: grid;
    align-content: center;
    text-align: center;
}
.wrapper{
    width: 100rem;
    text-align: left;
    margin: auto;
}
.color_themed_primary{
    color: $color_primary;
}
.color_themed_secondary{
    color: $color_secondary_light;
}
.color_green{
    color: $color_green;
}
.color-themed--primary,
.color--red,
.color_red{
    color: $color_red;
}
.txt_right{
    text-align: right;
}
// ACCESSIBILITY
button:focus,
a:focus{
    outline: none;
}
a:focus{
    text-decoration-line: underline;
}


@include desktop-sm{
    .wrapper{
        width: 100%;
        padding: 0 8rem;
    }
}
@include tablet{
    .p_larger{
        font-size: 2.5rem;
    }

}
@include mobile{
}
@include mobile-sm{
    .p_larger{
        font-size: 1.8rem;
    }
    .wrapper{
        padding: 0 4rem;
    }


}
